*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    font-family: 'Oswald', sans-serif;
}
.main-header{
    text-align: center;
    color: wheat;
}
.navbar{
    color: wheat;
}
.list-items{
    display: flex;
    width: 30%;
    margin: auto;
}
.itemlist{
    list-style: none;
    margin: 10px 30px;
    
    background-color: darkmagenta;
    color: white;
    border: 2px solid wheat;
    padding: 4px 10px;
    border-radius: 50px;
    cursor: pointer;
}
.url-link{
    width: 100%;
    text-decoration: none;
    color: white;
    width: 100%;
}

.movies,.web,.doc,.horror,.holly{
    width: 80%;
    height: auto;
    padding: 0 10px;
    margin: 10px auto;
    border-radius: 80px;
    border: 2px solid black;
    background-color: bisque;
}
.title-head{
    text-align: center;
}
.info{
    border: 2px solid red;
    background-color: lightpink;
    border: 2px solid red;
    border-radius: 50px;
    margin: 50px 10px;
    width: 95%;
    margin: 10px auto;
    height: auto;
    padding: 10px;
    
}
.cover-pic{
    width: 200px;
    height: 150px;
    margin: 0 8px;
    border-radius: 150px;
}
.intro-container{
    color: wheat;
}
.intro-head{
    text-align: center;
}
.timestamp{
    text-align: center;
    background-color: lightpink;
    padding: 6px 4px;
    width: 50%;
    margin:10px  auto;
    border: 2px solid red;
    color: black;
    border-radius: 40px;
}
.list{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    background-color:lightgray;
    width: 80%;
    height: auto;
    margin:20px  auto;
    color: black;
    border-radius: 60px;
}
.logo-img{
    width: 100px;
    height: 100px;
}

.details, .details2,.details3,.details4,.details5{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.doc-title{
    text-align: center;
}
.movie-head,.web-head{
    text-align: center;
}
.doc-head,.horror-head,.holly-head{
    text-align: center;
}
.plus, .multiply{
    cursor: pointer;
    background-color: lightblue;
    padding: 6px 4px;
    border-radius: 70px;
    border: 2px solid red;
    
}
.plus:hover{
    background-color:green;
    color: wheat;
}
.multiply:hover{
    background-color: red;
    color: wheat;
}
.btn{
    margin: 10px 40px;
    width: 90px;
    position: relative;
    cursor: pointer;
    padding: 8px ;
    background-color: darkmagenta;
    color: white;
    border-radius: 40px;
}
.btn:hover{
    background-color: black;
    color: wheat;
}
.link{
    color: white;
    text-decoration: none;
}
.footer{
    width: 100vw;
    padding: 10px auto;
    height: auto;
    background-color: lightpink;
    position: absolute;
}
.footer-head{
    text-align: center;
}

/* Media Queries */
@media(max-width:560px){
    .main-header{
        width: 60%;
        margin: auto;
        padding: 0 10px;
    }
    .list{
        padding: 24px;
    }
    .navbar{
        width: 30%; 
        margin: 10px 0;
    }
  
    .movies,.web,.doc,.horror,.holly{
       
        padding: 10px 30px;
        width: 80%;
        margin: 10px auto;
       
    }
 
    .info{
        
        margin: 50px 10px;
        width: 90%;
        margin: 20px auto;
        height: 300px;
        padding: 10px;
        
    }
    .cover-pic{
        width: 90%;
        height: 100px;
       
    }
    .btn-primary{
        width: 100px;
        margin: 10px 20px;
    }
    .btn{
        width: 60%;
        margin: 20px auto;
    }
    .movie-head,.web-head,.horror-head,.doc-head{
        padding: 0 10px;
    }
    .footer{
        padding: 10px auto;
    }
    .info{
        height: 380px;
    }
    .title-head{
        display: flex;
        flex-wrap: wrap;
        padding: 0 20px;
        margin: 10px 0;

    }
   
    
}